#skills {
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0 auto;
}

.skilltitle {
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.skilldescription {
    font-weight: 400;
    font-size: 1rem;
    max-width: 51rem;
    padding: 0 2rem;
}

.myskillbars {
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}

.skillbar {
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background-color: rgb(50, 50, 50);
}

.skillbarimg {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.skillbartext>p {
    font-size: 0.9rem;
    font-weight: 200;

}

@media screen and (max-width:720px) {
    .skillbartext {
        font-size: 2vw;
    }
}

@media screen and (max-width:480px) {
    .skillbartext>p {
        font-size: 3vw;
    }

    .skillbarimg {
        height: 2.25rem;
        width: 2.25rem;
    }

    .skillbartext>h2 {
        font-size: 5vw;
    }
}