.navbar {
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    padding: 1rem 2rem;
    align-items: center;
    display: flex;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 5;
    color: white;
    background-color: rgb(30, 30, 30);
}

.navlistItem {
    margin: 1rem;
    cursor: pointer;
}

.navlistItem:hover {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;

}

.contect_img {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}

.navbutton {
    color: black;
    background-color: #ffffffea;
    border: none;
    border-radius: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: medium;
    padding: 0px 1rem;
    padding-right: 2rem;
    cursor: pointer;
    transition: background-color 0.3s ease; 
}

.navbutton:active {
    background-color: #dddddd;
}

.logo {
    object-fit: cover;
    width: 6rem;
}


.active {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.listItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

.mobmenu {
    display: none;
    object-fit: cover;
    height: 1.8rem;
}

.burgermenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;
}

@media screen and (max-width:720px) {
    .mobmenu {
        display: flex;
    }

    .navmenu {
        display: none;
    }

    .navbutton {
        display: none;
    }
}